			              "avatar": "http://static.galileo.xiaojukeji.com/static/tms/default_header.png"
			            }
			          ],
			          "icon": "http://fuss10.elemecdn.com/a/94/7371083792c19df00e546b29e344cjpeg.jpeg?imageView2/1/w/114/h/114",
			          "image": "http://fuss10.elemecdn.com/a/94/7371083792c19df00e546b29e344cjpeg.jpeg?imageView2/1/w/750/h/750"
			        }
			      ]
			    }
			  ]
			let i
			let len = this.goods.length
			for (i = 0; i < len; i++) {
				let support = {
					type: this.goods[i].type === -1 ? -1 : 5,
					description: this.goods[i].name
				}
				this.supports.push(support)
			}

			this.$nextTick(() => {
				this._initScroll()
				this._calculateHeight()
			})
		},
		components: {
			'v-support': support,
			'v-shopcart': shopcart,
			'v-cartcontrol': cartcontrol,
			'v-food': food
		},
		methods: {
			_initScroll() {
				this.menuScroll = new BScroll(this.$els.menuWrapper, {
					click: true
				})
				this.foodsScroll = new BScroll(this.$els.foodsWrapper, {
					probeType: 3,
					click: true
				})

				this.foodsScroll.on('scroll', (pos) => {
					let _scrollY = Math.abs(Math.round(pos.y))
					for (let i = 1; i < this.listHeight.length; i++) {
						if (_scrollY >= this.listHeight[i - 1] && _scrollY < this.listHeight[i]) {
							this.scrollY = i - 1
						}
					}
				})
			},
			_calculateHeight() {
				let foodsList = this.$els.foodsWrapper.getElementsByClassName('foods')
				// this.listHeight = []
				this.listHeight.push(0)
				for (let i = 0; i < foodsList.length; i++) {
					let height1 = foodsList[i].clientHeight
					let height2 = this.listHeight[i] + height1
					this.listHeight.push(height2)
				}
			},
			selectMenu(index, event) {
				if (!event._constructed) {
					return
				}
				let foodList = this.$els.foodsWrapper.getElementsByClassName('foods')
				let el = foodList[index]
				this.foodsScroll.scrollToElement(el, 300)
				// this.foodsScroll.scrollTo(0, this.listHeight[index], 300)
			},
			_drop(target) {
				// 动画优化
				this.$nextTick(() => {
					this.$refs.shopcart.drop(target)
				})
			},
			selectFoodAction(food, e) {
				if (!e._constructed) {
					return
				}
				this.selectFood = food
				this.$refs.food.show()
			}
		},
		props: {
			seller: Object
		},
		computed: {
			selectFoods: function() {
				let foods = []
				this.goods.forEach((good) => {
					good.foods.forEach((food) => {
						if (food.count) {
							foods.push(food)
						}
					})
				})
				return foods
			}
		},
		events: {
			addCart(target) {
				this._drop(target)
			}
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import '../../common/scss/mixin.scss';
	@import '../../common/scss/base.scss';
	.goods{
		display: flex;
		position: absolute;
		top: 170px;
		left: 0;
		right: 0;
		bottom: 48px;
		overflow: hidden;
		.menu-wrapper{
			width: 80px;
			background-color: #f3f5f7;

			.menu-list{
				display: table;
				.menu-item{
					padding: 0px 12px;
					background-color: #f3f5f7;
					.cell{
						display: table-cell;
						vertical-align: middle;
						height: 54px;
						width: 66px;
						line-height: 14px;
						font-size: 12px;
						color: rgb(77,85,93);
						@include border-1px(rgba(7,17,27,.1));
					}
					&:last-child .cell{
						@include border-none;
					}
					&.current{
						background-color: white;
						.cell{
							@include border-none;
						}
						
					}	
				}
			}
		}
		.foods-wrapper{
			flex: 1;
			.food-title{
				height: 26px;
				line-height: 26px;
				padding-left: 14px;
				font-size: 12px;
				color: rgb(147,153,159);
				background-color: #f3f5f7;
				border-left: 2px solid #d9dde1;
			}
			.food-list{
				.food-item{
					display: flex;
					margin: 18px;
					padding-bottom: 18px;
					@include border-1px(rgba(7,17,27,.1));
					&:last-child{
						@include border-none;
						margin-bottom: 0;
					}
					.avatar{
						display: inline-block;
						> img{
							width: 57px;
							height: 57px;
						}
					}
					.content{
						position: relative;
						flex: 1;
						display: inline-block;
						padding-left: 10px;
						vertical-align: top;
						.name{
							font-size: 14px;
							font-weight: 700;
							margin-top: 2px;
						}
						.desc,.extra{
							margin-top: 8px;
							font-size: 10px;
							color: rgb(147,153,159);
						}
						.extra .sellCount{
							margin-right: 12px;
						}
						.price{
							line-height: 24px;
							>span >font{
								font-size: 10px;
							}
							.new{
								font-size: 14px;
								margin-right: 8px;
								font-weight: bold;
								color: rgb(240,20,20);
							}
							.old{
								font-size: 10px;
								font-weight: 700;
								color: rgb(147,153,159);
								text-decoration: line-through;
							}
						}
						.cartcontrol-wrapper{
							position: absolute;
							bottom: 0;
							right: 0;
						}


					}
				}
			}
		}
	}
</style>


